//top header
.header{
    background: $color-white;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    .logo-brand{
        height: 3.5rem;
        padding-left: 1rem;
        img{
            height: 100%;
        }
    }
    .search{
        position: relative;
        button{
            position: absolute;
            top: 50%;
            right: 1rem;
            border: none;
            transform: translateY(-50%);
            background: $color-grey-light-2;
            i{
                font-size: 1.6rem;
                color: $color-grey-dark-2;
            }
        }
    }
    .notify{
        ul{
            list-style: none;
            li{
                padding: 2.5rem 1.5rem;
                display: inline-block;
                position: relative;
                cursor: pointer;
                &.notify__bookmark:after{
                    @include notify_amount;
                    content: '7';
                }
                &.notify__chat:after{
                    @include notify_amount;
                    content: '10';
                }
                & > i {
                    font-size: 2.2rem;
                }
                &:hover{
                    background: $color-grey-light-4;
                }
            }
        }
        &__avatar{
            &__block{
                position: relative;
                padding-left: 4.5rem;
                img{
                    max-width:3.5rem;
                    border-radius: 50%;
                    position: absolute;
                    left: 0;
                    top: -7px;
                }
            }
        }
    }
}

//content
#content{
    display: flex;
    flex-direction: row;
}
.navigation{
    width: 20%;
    height: 100vh;
    background:$color-grey-dark-1;
    &__nav{
        list-style: none;
    }
    &__item{
        color: $color-grey-light-1;
        padding: 10px 30px;
        text-transform: uppercase;
        margin-bottom: 3px;
        transition: all 300ms ease;
        &.active{
            background: $color-red-medium;
        }
        &:hover{
            background: $color-red-medium;
        }
        i{
            margin-right: 10px;
            font-size: 1.7rem;
        }
    }
}
.content{
    width: 80%;
    height: 100vh;
    background: $color-grey-light-1;
}